body {
  margin: 0;
  font-family: sans-serif;
}

.mark {
  color: aquamarine;
}

#root {
  min-height: 100vh;
  background-color: coral;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 20px;
}

.card {
  width: 120px;
  height: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  border: 1px solid;
}

.box {
  display: grid;
  grid-template-columns: repeat(3, auto);
  gap: 10px;
}